body {
  margin: 0;
  font-family: "Manrope", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  font-weight: 400;
  scroll-behavior: smooth;
}

.project-title {
  font-size: 40px;
  font-weight: 600;
  line-height: 60px;
}

.project-sub-title {
  color: #212529;
  font-size: 40px;
  font-weight: 400;
  line-height: 46px;
}

.project-content {
  color: #545454;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.section-title {
  font-size: 16px;
  font-weight: 700;
  text-transform: capitalize;
}

.keyword-in-title {
  font-size: 35px;
  font-style: italic;
  line-height: 48px;
}

.topic-title {
  font-size: 40px;
  line-height: 48px;
}

.topic-desc {
  color: #545454;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.info-title {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

.info-content {
  color: #212529;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.dark-section-no {
  font-size: 32px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 1.6px;
  text-transform: uppercase;
}

.dark-section-title {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

.dark-section-summary {
  color: #a0a0a0;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.button {
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 18px;
  font-weight: 600;
  line-height: 27px;
  padding: 12px 20px;
  background-color: #000;
  border-radius: 12px;
  color: #fff;
}

code {
  font-family: "Manrope", sans-serif;
}

.navbar {
  background: #fff;
  width: 100%;
  z-index: 99999999;
  font-weight: 600;
  font-family: manrope, sans-serif;
  padding: 0;
  border-bottom: 1px solid #d6d6db;
}

.navbar {
  display: flex;
  width: 100%;
  margin: auto;
  padding: 0 32px;
}

.navbar a {
  text-decoration: none;
}

.navbar-brand {
  padding: 0;
  flex: 0 1;
  margin: 0 32px;
}

.nav-link {
  font-family: manrope, sans-serif;
  font-weight: 700;
  font-size: 16px;
  margin: 0;
  padding: 0 16px !important;
}

.navbar-nav {
  align-items: center;
  margin: auto;
  padding: 34px;
  grid-column-gap: 4px;
  -webkit-column-gap: 4px;
          column-gap: 4px;
}

.navbar-nav .active {
  align-items: center;
  border: 0;
}

.navbar-light .navbar-toggler {
  color: var(--darkPrimary);
  border: none;
}

.hidden-brand {
  display: none;
}

.hidden-brand .navbar-brand {
  margin: 0;
  padding: 24px 0;
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
  color: #000;
  background: #eee;
  border-radius: 20px;
}

.navbar-light .navbar-nav .nav-link {
  color: #000;
  padding: 10px 20px !important;
}

.navbar .navbar-collapse {
  text-align: left;
}

.brand {
  display: flex;
  align-items: center;
}

.active {
  color: #000;
  font-weight: 600 !important;
  border-bottom: none !important;
  background: #ededed;
  color: #000 !important;
  border-radius: 1000px;
}

.brand_logo {
  width: 48px;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease; 
  
}

.brand_logo:hover {

    -webkit-transform: rotate(360deg);

            transform: rotate(360deg); 
}



.navbar-toggler-icon {
  color: #00f;
}

.nav-link {
  padding: 0 1.5rem !important;
}

.navbar-light .navbar-nav .nav-link {
  color: #303a49;
}

.navbar-toggler {
  background-color: #fff !important;
}

@media screen and (max-width: 1501px) {
  .navbar-nav {
    background-color: #fff !important;
    border-radius: 100px;
  }
}

@media screen and (max-width: 991px) {
  .navbar-nav .navbar-brand {
    display: none;
  }

  .navbar-light .navbar-nav .nav-link {
    border-bottom: none;
    padding: 16px 24px !important;
  }

  .hidden-brand,
  .nav-button.toggle-mob {
    display: block;
  }

  .nav-button.toggle-mob {
    display: block;
  }

  .collapse.show,
  .collapsing {
    background: hsla(0, 0%, 100%, 0.9333333333333333);
    margin: 10px -40px;
  }

  .navbar .navbar-brand {
    float: left;
  }

  .navbar .navbar-toggler {
    float: right;
  }

  .navbar .navbar-collapse {
    clear: both;
    background-color: #fff;
    padding: 10px;
  }

  .navbar-nav {
    text-align: left;
  }

  .navbar-nav a {
    padding: 8px 0 !important;
  }
}

.footer {
  padding: 24px 0px;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-top: 1px solid hsla(0, 0%, 100%, 0.12);
}

.footer img {
  width: 70px;
}

.footer h4 {
  font-size: 48px;
  color: #fff;
  margin-top: 72px;
  text-align: left;
  line-height: 130%;
}

.footer-links {
  width: 100%;
  max-width: 1240px;
  padding: 64px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer h3 {
  color: #7b8292;
  text-align: center;
  margin: 0 0 40px;
}

.footer-grid {
  display: grid;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  grid-column-gap: 16px;
  grid-template-columns: repeat(3, 1fr);
}

.footer p {
  font-size: 16px;
  color: #fff;
  margin: 0;
}

@media screen and (max-width: 991px) {
  .footer-links {
    margin: 24px 0px;
    padding: 0;
  }

  .footer h4 {
    margin-top: 48px;
    font-size: 40px;
  }
}

